import { createSlice, configureStore } from '@reduxjs/toolkit';
import { Product } from '../model/Product';

export interface MenhirStoreState {
  Cart: Array<Product>;
  IsAuthenticated: boolean;
  Username: string;
  PaymentMethod: number;
  ShippingMethod: number;
}

const initMenhirStoreState: MenhirStoreState = { Cart: [], IsAuthenticated: false, Username: '', PaymentMethod: -1, ShippingMethod : -1 };

const  menhirStoreSlice = createSlice({
  name : 'menhirStore',
  initialState: initMenhirStoreState,
  reducers: {
    add(state, action) {
      state.Cart.push(action.payload.product);
    },
    rem(state, action) {
      state.Cart = state.Cart.filter(i => i['Code'] !== action.payload.product.Code);
    },
    clear(state, action) {
      state.Cart = [];
    },
    login(state, action) {
      state.IsAuthenticated = action.payload.isAuthenticated;
      state.Username = action.payload.username;
    },
    logoff(state, action) {
      state.IsAuthenticated = false;
      state.Username = '';
    },
    setPaymentMethod(state, action) {
      state.PaymentMethod = action.payload.paymentMethod;
    },
    setShippingMethod(state, action) {
      state.ShippingMethod = action.payload.shippingMethod;
    }
  }
});

const store = configureStore({
  reducer: menhirStoreSlice.reducer
});

export const cartActions = menhirStoreSlice.actions;
export default store;