import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import {
  Box,
  Flex,
  Text,
  Button,
  Table,
  Tr,
  Th,
  Td,
  Thead,
  Tbody,
  Spacer,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";
import MenhirPageBusy, { BusyProperties } from "./PageBusy";
import axios from "axios";
import { Order } from "./model/TransportOrder";
import { MenhirStoreState } from "./store";
import { AiOutlineArrowRight, AiOutlineArrowLeft } from "react-icons/ai";

export default function MenhirAdmin(props: any) {
  const username = useSelector((state: MenhirStoreState) => state.Username);
  const [orders, setOrders] = useState<Array<Order>>([]);
  const [ordersCount, setOrdersCount] = useState(0);
  const [busy, setBusy] = useState(false);
  const [page, setPage] = useState(0);

  const getData = () => {
    setBusy(true);
    axios.defaults.withCredentials = true;
    axios(
      `${process.env.REACT_APP_BACKEND_URL}/orders?username=${username}&page=${page}`,
      {
        method: "get",
      }
    )
      .then((res) => {
        let orders = res.data;
        if (orders === null) return;
        setOrders(orders);
        setBusy(false);
      })
      .catch((r) => {
        alert(`Error: ${r}`);
      })
      .finally(() => setBusy(false));
  };

  const getPageCount = () => {
    setBusy(true);
    axios.defaults.withCredentials = true;
    axios(
      `${process.env.REACT_APP_BACKEND_URL}/orders/count?username=${username}`,
      {
        method: "get",
      }
    )
      .then((res) => {
        let ordersCount = res.data;
        setOrdersCount(ordersCount);
        setBusy(false);
      })
      .catch((r) => {
        alert(`Error: ${r}`);
      })
      .finally(() => setBusy(false));
  };

  const getDataStart = () => {
    setPage(0);
    getData();
  };

  const getDataPrevious = () => {
    if (page === 0) return;
    setPage((page) => page - 1);
    getData();
  };

  const getDataNext = () => {
    setPage((page) => page + 1);
    getData();
  };

  const openOrder = (orderId: number | undefined) => {
    console.log(orderId);
  };

  const formatDate = (dateStr: string) => {
    if (!dateStr) return "";
    const dateObj = new Date(dateStr);
    const options = {
      hour12: false,
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    };
    return dateObj.toLocaleDateString(navigator.language, options);
  };

  const calculatePageNumber = () => {
    const pageLabel = page + 1;
    return `Pagina ${pageLabel} di ${Math.ceil(ordersCount / 10)}`;
  };

  const writePaymentMethod = (method: number) => {
    switch (method) {
      case 0:
        return "In negozio";
      case 1:
        return "In contanti";
      case 2:
        return "Carta di credito";
      case 3:
        return "PayPal";
      default:
        return method.toString();
    }
  };

  const writeShippingMethod = (method: number) => {
    switch (method) {
      case 0:
        return "Ririto in negozio";
      case 1:
        return "Consegna a casa";
      default:
        return method.toString();
    }
  };

  useEffect(() => {
    getData();
    getPageCount();
  }, [page, ordersCount]);

  const busyProps: BusyProperties = {
    Size: "large",
    Text: "Stiamo caricando i dati..",
  };

  return (
    <Box>
      {busy && <MenhirPageBusy {...busyProps} />}

      <Flex color="white">
        {!busy && (
          <Box flex="1" p={0}>
            <Box>
              <Text as="h3" mt={{ base: 0, md: 5 }}>
                Gestione ordini
              </Text>
            </Box>
            <Box mt={4} mb={8} display="flex">
              <Button
                bg="brand.100"
                color="#ffffff"
                _hover={{ bgColor: "brand.100shade" }}
                size="md"
                fontWeight="normal"
                onClick={getDataStart}
                mr={5}
              >
                Ricarica ordini
              </Button>
              <Spacer />
              <Button
                bg="brand.100"
                color="#ffffff"
                _hover={{ bgColor: "brand.100shade" }}
                size="md"
                fontWeight="normal"
                onClick={getDataPrevious}
                mr={5}
              >
                <AiOutlineArrowLeft />
              </Button>
              <Box display="inline" mr={6} ml={2} mt={2}>
                <Text>{calculatePageNumber()}</Text>
              </Box>
              <Button
                bg="brand.100"
                color="#ffffff"
                _hover={{ bgColor: "brand.100shade" }}
                size="md"
                fontWeight="normal"
                onClick={getDataNext}
              >
                <AiOutlineArrowRight />
              </Button>
            </Box>

            <Table mt={10} border="solid 1px #f0f0f0">
              <Thead>
                <Tr>
                  <Th>Codice ordine</Th>
                  <Th>Utente</Th>
                  <Th>Data ordine</Th>
                  <Th>Importo totale</Th>
                  <Th>Modalit di spedizione</Th>
                  <Th>Modalit di pagamento</Th>
                  <Th>Consegnato</Th>
                </Tr>
              </Thead>
              <Tbody>
                {orders.map((k, i) => (
                  <Tr key={i}>
                    <Td isNumeric>
                      <Link
                        to={{
                          pathname: "/orderdetail",
                          state: { orderId: `${k.orderId}` }
                        }}
                      >
                        <Text as="p">{k.orderId}</Text>
                      </Link>
                    </Td>
                    <Td isNumeric>
                      <Text as="p">{k.email}</Text>
                    </Td>
                    <Td isNumeric>
                      <Text as="p">{formatDate(k.created)}</Text>
                    </Td>
                    <Td isNumeric>
                      <Text as="p">
                        {new Intl.NumberFormat("it-IT", {
                          style: "currency",
                          currency: "EUR",
                        }).format(k.total)}
                      </Text>
                    </Td>
                    <Td isNumeric>
                      <Text as="p">
                        {writeShippingMethod(k.shippingMethod)}
                      </Text>
                    </Td>
                    <Td isNumeric>
                      <Text as="p">{writePaymentMethod(k.paymentMethod)}</Text>
                    </Td>
                    <Td>
                      {k.shipped && <Text as="p">Si</Text>}
                      {!k.shipped && <Text as="p">No</Text>}
                    </Td>
                  </Tr>
                ))}
              </Tbody>
            </Table>
          </Box>
        )}
      </Flex>
    </Box>
  );
}
