import { ReactNode } from 'react';
import logo from "./logo.png";
import {
  Box,
  Container,
  SimpleGrid,
  Stack,
  Text,
  useColorModeValue,
  Image
} from '@chakra-ui/react';
import { Link } from 'react-router-dom';

const Logo = (props: any) => {
  return (
<Image src={logo} alt="React Menhir Shop" />
  );
};

const ListHeader = ({ children }: { children: ReactNode }) => {
  return (
    <Text fontWeight={'500'} fontSize={'lg'} mb={2}>
      {children}
    </Text>
  );
};

export default function LargeWithLogoLeft() {
  return (
    <Box
      color={useColorModeValue('gray.700', 'gray.400')} mt={8}>
      <Container as={Stack} maxW={'6xl'} py={10}>
        <SimpleGrid
          templateColumns={{ sm: '1fr 1fr', md: '2fr 1fr 1fr 1fr 1fr' }}
          spacing={8}>
          <Stack spacing={6}>
            <Box>
              <Logo color={useColorModeValue('gray.50', 'white')} />
            </Box>
            <Text fontSize={'sm'}>
               2021 Il Tagliere di Mario
            </Text>
          </Stack>
          <Stack align={'flex-start'}>
            <ListHeader>Negozio</ListHeader>
            <Link to="/about" >Chi siamo</Link>
          </Stack>
          <Stack align={'flex-start'}>
            <ListHeader>Assistenza</ListHeader>
            <Link to="/" href={'#'}>Help Center</Link>
            <Link to="/" href={'#'}>Termini di Servizio</Link>
            <Link to="/" href={'#'}>Note legali</Link>
            <Link to="/" href={'#'}>Privacy</Link>
          </Stack>
          <Stack align={'flex-start'}>
            <ListHeader>Seguici</ListHeader>
            <Link to="/" href={'#'}>Instagram</Link>
          </Stack>
        </SimpleGrid>
      </Container>
    </Box>
  );
}