import React, { useState, useEffect } from "react";
import './Browser.css';

import {
  Box,
  Flex,
  SimpleGrid,
  Text
} from "@chakra-ui/react";

import MenhirWindow from "./Window";
import MenhirMiniProduct from "./MiniProduct";
import MenhirPageBusy, { BusyProperties } from "./PageBusy";
import axios from "axios";
import { Product } from "./model/Product";

export default function MenhirBrowser(props:any) {

  const [data, setData] = useState<Array<Product>>([]);
  const [catTitle, setCatTitle] = useState('TUTTI I PRODOTTI');
  const [busy, setBusy] = useState(false);
  const getData = () => {
    setBusy(true);
    axios.get(`${process.env.REACT_APP_BACKEND_URL}/products`).then((res) => {
      let prods = res.data;
      const catId = props.location.state ? props.location.state[0]['catId'] : undefined;
      if (prods === null) return;

      if (catId && catId !== 'ALL') {
        setData(prods.filter(k => k.category === parseInt(catId)));
        setCatTitle(props.location.state[1]['catDesc']);
      }
      else {
        setData(prods.sort());
      }

      setBusy(false);
    });
  };  

  useEffect(() => {
    getData();
  }, props.location.state || []);

  const busyProps: BusyProperties = {
    Size: "large",
    Text: "Stiamo caricando i dati.."
  };

  return (
    <Box>
      {busy && <MenhirPageBusy {...busyProps} />}
      {!busy && (
      <Flex color="white">
        <Box flex="1" p={0}>
          <MenhirWindow showHomeBanner={false} />
          <Box><Text as="h3" mt={{ base:0, md:5}}>{catTitle}</Text></Box>
          <SimpleGrid minChildWidth="175px" columns={2} spacing="4px" ml={1}>
            {data.map((item, index) => (
              <MenhirMiniProduct
              key={index}
              code={item.code}
              title={item.title}
              description={item.description}
              discount={item.discount}
              price={item.price}
              category={item.category}
              categoryDescription={catTitle}
              rating={item.rating}
              imgName={item.imgName}
              minimalView={true}
              longDescription={item.longDescription}
              uom={item.uom}
              ></MenhirMiniProduct>
            ))}
          </SimpleGrid>          
        </Box>
      </Flex>
      )}
    </Box>
  );
}
