import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import "./Checkout.css";
import {
  Alert,
  AlertTitle,
  AlertDescription,
  AlertIcon,
  CloseButton,
  Center,
  Box,
  Select,
  Text,
  FormControl,
  FormLabel,
  SimpleGrid,
  Input,
  Flex,
  Button,
  FormErrorMessage,
} from "@chakra-ui/react";
import { MenhirStoreState } from "./store";
import axios from "axios";
import { Customer } from "./model/Customer";
import { AccountActivation } from "./model/accountvalidation";
import * as Yup from "yup";
import MenhirPageBusy, { BusyProperties } from "./PageBusy";

const accountValidationSchema = Yup.object().shape({
  email: Yup.string().email("EMAIL_INV").required("EMAIL_REQ"),
  password: Yup.string()
    .required("PSW_REQ")
    .min(6, "PSW_MIN")
    .max(50, "PSW_MAX"),
  passwordConfirmation: Yup.string().oneOf(
    [Yup.ref("password"), null],
    "PSW_MATCH"
  ),
  name: Yup.string()
    .required("NAME_REQ")
    .min(2, "NAME_MIN")
    .max(50, "NAME_MAX"),
  lastname: Yup.string()
    .required("LASTNAME_REQ")
    .min(2, "LASTNAME_MIN")
    .max(50, "LASTNAME_MAX"),
  address: Yup.string()
    .required("ADDR_REQ")
    .min(10, "ADDR_MIN")
    .max(100, "ADDR_MAX"),
  city: Yup.string()
    .required("CITY_REQ")
    .min(3, "CITY_MIN")
    .max(50, "CITY_MAX"),
  country: Yup.string()
    .required("COUNTRY_REQ")
    .min(3, "COUNTRY_MIN")
    .max(30, "COUNTRY_MAX"),
  region: Yup.string()
    .required("REGION_REQ")
    .min(3, "REGION_MIN")
    .max(30, "REGION_MAX"),
  county: Yup.string()
    .required("COUNTY_REQ")
    .min(3, "COUNTY_MIN")
    .max(30, "COUNTY_MAX"),
  zipcode: Yup.string().required("ZIP_REQ").min(5, "ZIP_MIN").max(5, "ZIP_MAX"),
  phone: Yup.string().required("PHO_REQ").min(10, "PHO_MIN").max(30, "PHO_MAX"),
});

const plainRegions = [
  "Abruzzo",
  "Basilicata",
  "Calabria",
  "Campania",
  "Emilia Romagna",
  "Friuli Venezia Giulia",
  "Lazio",
  "Liguria",
  "Lombardia",
  "Marche",
  "Molise",
  "Puglia",
  "Sardegna",
  "Sicilia",
  "Toscana",
  "Trentino-Alto Adige",
  "Umbria",
  "Valle d'Aosta",
  "Veneto",
];

const regions = {
  Abruzzo: ["Chieti", "L'aquila", "Pescara", "Teramo"],
  Basilicata: ["Matera", "Potenza"],
  Calabria: ["Catanzaro", "Cosenza", "Crotone", "Reggio Calabria"],
  Campania: ["Avellino", "Benevento", "Caserta", "Napoli", "Salerno"],
  "Emilia Romagna": [
    "Ferrara",
    "Forl",
    "Modena",
    "Parma",
    "Piacenza",
    "Ravenna",
    "Reggio Emilia",
    "Rimini",
  ],
  "Friuli Venezia Giulia": ["Gorizia", "Pordenone", "Trieste", "Udine"],
  Lazio: ["Frosinone", "Latina", "Rieti", "Roma"],
  Liguria: ["Genova", "Imperia", "La Spezia", "Savona"],
  Lombardia: [
    "Bergamo",
    "Brescia",
    "Como",
    "Cremona",
    "Lecco",
    "Lodi",
    "Mantova",
    "Milano",
    "Monza e della Brianza",
    "Pavia",
    "Sondrio",
    "Varese",
  ],
  Marche: ["Ancona", "Ascoli Piceno", "Fermo", "Macerata"],
  Molise: ["Campobasso", "Isernia"],
  Puglia: ["Bari", "Barletta-Andria-Trani", "Brindisi", "Foggia"],
  Sardegna: [
    "Cagliari",
    "Carbonia-Iglesias",
    "Nuoro",
    "Olbia-Tempio",
    "Oristano",
    "Medio Campidano",
    "Sassari",
    "Ogliastra",
  ],
  Sicilia: [
    "Agrigento",
    "Caltanissetta",
    "Catania",
    "Enna",
    "Messina",
    "Palermo",
    "Ragusa",
    "Siracusa",
    "Trapani",
  ],
  Toscana: [
    "Arezzo",
    "Firenze",
    "Grosseto",
    "Livorno",
    "Lucca",
    "Massa-Carrara",
    "Pisa",
    "Pistoia",
    "Prato",
    "Siena",
  ],
  "Trentino-Alto Adige": ["Bolzano", "Trento"],
  Umbria: ["Perugia", "Terni"],
  "Valle d'Aosta": ["Aosta"],
  Veneto: [
    "Belluno",
    "Padova",
    "Rovigo",
    "Treviso",
    "Venezia",
    "Verona",
    "Vicenza",
  ],
};

export default function MenhirAccount(props: any) {
  const dispatch = useDispatch();
  axios.defaults.withCredentials = true;
  const isAuth = useSelector(
    (state: MenhirStoreState) => state.IsAuthenticated
  );
  const username = useSelector((state: MenhirStoreState) => state.Username);
  const [password, setPassword] = useState("");
  const [passwordConfirmation, setPasswordConfirmation] = useState("");
  const [counties, setCounties] = useState([]);
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const [lastname, setLastname] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [city, setCity] = useState("");
  const [zipcode, setZipcode] = useState("");
  const [selectedRegion, setSelectedRegion] = useState("");
  const [selectedCounty, setSelectedCounty] = useState("");
  const [country, setCountry] = useState("ITALY");
  const [viewState, setViewState] = useState(0); // 0 input account data, 1 account created and waiting code, 2 account validated
  const [authCode, setAuthCode] = useState("");
  const [busy, setBusy] = useState(false);
  const [viewErrorTitle, setViewErrorTitle] = useState("");
  const [viewErrorDescription, setViewErrorDescription] = useState("");
  const [formErrors, setFormErrors] = useState([]);

  let pageTitle = "";

  switch (viewState) {
    case 0:
      pageTitle = "CREAZIONE ACCOUNT";
      break;
    case 1:
      pageTitle = "VALIDAZIONE ACCOUNT";
      break;
    case 2:
      pageTitle = "ACCOUNT PRONTO ALL'USO";
      break;
    default:
      pageTitle = "CREAZIONE ACCOUNT";
  }

  const getData = async () => {
    axios.defaults.withCredentials = true;
    axios(
      `${process.env.REACT_APP_BACKEND_URL}/customers?username=${username}`,
      {
        method: "get",
      }
    )
      .then((res) => {
        let customer: Customer = res.data;
        if (customer === null) throw new Error("Error");
        setCustomer(customer);
        for (const property in regions) {
          if (property === customer.region) setCounties(regions[property]);
        }
      })
      .catch((e) => {
        alert("error reading customer info");
      });
  };

  const regionChange = (event) => {
    if (event.target.value) {
      setSelectedRegion(event.target.value);
      setCounties(regions[event.target.value]);
    }
  };

  const countyChange = (event) => {
    if (event.target.value) {
      setSelectedCounty(event.target.value);
    }
  };

  const validateAccount = () => {
    const activationToken: AccountActivation = {
      Email: email,
      Code: authCode,
    };

    setBusy(true);

    axios(`${process.env.REACT_APP_BACKEND_URL}/activations`, {
      data: activationToken,
      withCredentials: true,
      method: "post",
    })
      .then((res) => {
        setViewState(2);
        setBusy(false);
      })
      .catch((err) => {
        setBusy(false);
        setViewErrorTitle("Oops! Si  verificato un errore");
        setViewErrorDescription(
          `Siamo spiacenti, non abbiamo potuto attivare il tuo account: ${err}`
        );
      });
  };

  const checkError = (errCode: string): boolean => {
    const err = formErrors.find((e) => e === errCode);
    return err !== null && err !== undefined;
  };

  const createAccount = () => {
    setViewErrorTitle("");
    const customer: Customer = {
      email: email,
      name: name,
      lastname: lastname,
      password: password,
      passwordConfirmation: passwordConfirmation,
      phone: phone,
      address: address,
      city: city,
      zipcode: zipcode,
      country: country,
      region: selectedRegion,
      county: selectedCounty,
    };

    accountValidationSchema
      .validate(
        {
          email: customer.email,
          name: customer.name,
          lastname: customer.lastname,
          password: customer.password,
          passwordConfirmation: customer.passwordConfirmation,
          phone: customer.phone,
          address: customer.address,
          city: customer.city,
          zipcode: customer.zipcode,
          country: customer.country,
          region: customer.region,
          county: customer.county,
        },
        { abortEarly: false }
      )
      .then((res) => {
        if (res) {
          setBusy(true);
          axios(`${process.env.REACT_APP_BACKEND_URL}/accounts`, {
            data: customer,
            withCredentials: true,
            method: "post",
          })
            .then((res) => {
              setBusy(false);
              setViewState(1);
            })
            .catch((err) => {
              setBusy(false);
              setViewErrorTitle("Oops! Si  verificato un errore");
              setViewErrorDescription(
                `Siamo spiacenti, la creazione del tuo account non ha avuto successo: ${err}`
              );
              // tolog
            });
        }
      })
      .catch((err) => {
        console.log(err.name);
        console.log(err.errors);
        setFormErrors(err.errors);
      });
  };

  const busyProps: BusyProperties = {
    Size: "large",
  };

  useEffect(() => {
    //getData();
  }, []);

  return (
    <Box>
      <Text as="h3" mt={{ base: 0, md: 5 }} mb={{ base: 0, md: 10 }}>
        {pageTitle}
      </Text>

      {viewErrorTitle && (
        <Alert status="error" mb={{ base: 0, md: 5 }}>
          <AlertIcon />
          <AlertTitle mr={2}>{viewErrorTitle}</AlertTitle>
          <AlertDescription>{viewErrorDescription}</AlertDescription>
          <CloseButton position="absolute" right="8px" top="8px" />
        </Alert>
      )}
      <Flex w="100%">
        {busy && <MenhirPageBusy {...busyProps} />}

        {!busy && (
          <Box flex="1" p={0}>
            {viewState === 0 && (
              <Box>
                <SimpleGrid columns={2} spacing={5} minChildWidth="300px" p={2}>
                  <Box className="menhir-input-form">
                    <Text as="h4" mt={0} mb={4} fontWeight="bold">
                      DATI DEL TUO ACCOUNT
                    </Text>
                    <FormControl
                      isRequired
                      isInvalid={
                        checkError("EMAIL_REQ") || checkError("EMAIL_INV")
                      }
                    >
                      <FormLabel>Email</FormLabel>
                      <Input
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        type="text"
                      />
                      {checkError("EMAIL_REQ") && (
                        <FormErrorMessage>
                          Questo campo  obbligatorio
                        </FormErrorMessage>
                      )}
                      {checkError("EMAIL_INV") && (
                        <FormErrorMessage>
                          Questo indirizzo di email indicato non  valido
                        </FormErrorMessage>
                      )}
                    </FormControl>
                    <SimpleGrid columns={2} spacing={4} mt={4}>
                      <FormControl
                        isRequired
                        isInvalid={
                          checkError("PSW_REQ") ||
                          checkError("PSW_MIN") ||
                          checkError("PSW_MAX")
                        }
                      >
                        <FormLabel>Scegli password</FormLabel>
                        <Input
                          value={password}
                          onChange={(e) => setPassword(e.target.value)}
                          type="password"
                        />
                        {checkError("PSW_REQ") && (
                          <FormErrorMessage>
                            Questo campo  obbligatorio
                          </FormErrorMessage>
                        )}
                        {checkError("PSW_MIN") && (
                          <FormErrorMessage>
                            Occorrono almeno 6 caratteri
                          </FormErrorMessage>
                        )}
                        {checkError("PSW_MAX") && (
                          <FormErrorMessage>
                            Troppo lungo, si prega di non superare 50 caratteri
                          </FormErrorMessage>
                        )}
                      </FormControl>
                      <FormControl
                        isRequired
                        isInvalid={checkError("PSW_MATCH")}
                      >
                        <FormLabel>Conferma password</FormLabel>
                        <Input
                          value={passwordConfirmation}
                          type="password"
                          onChange={(e) =>
                            setPasswordConfirmation(e.target.value)
                          }
                        />
                        {checkError("PSW_MATCH") && (
                          <FormErrorMessage>
                            La password di conferma non combacia con la
                            password. Perfavore verifica di averle inserite
                            uguali.
                          </FormErrorMessage>
                        )}
                      </FormControl>

                      <FormControl
                        isRequired
                        isInvalid={
                          checkError("NAME_REQ") ||
                          checkError("NAME_MIN") ||
                          checkError("NAME_MAX")
                        }
                      >
                        <FormLabel>Nome</FormLabel>
                        <Input
                          value={name}
                          onChange={(e) => setName(e.target.value)}
                          type="text"
                        />
                        {checkError("NAME_REQ") && (
                          <FormErrorMessage>
                            Il nome  obbligatorio
                          </FormErrorMessage>
                        )}
                        {checkError("NAME_MIN") && (
                          <FormErrorMessage>
                            Occorrono almeno 2 caratteri
                          </FormErrorMessage>
                        )}
                        {checkError("NAME_MAX") && (
                          <FormErrorMessage>
                            Troppo lungo, si prega di non superare 50 caratteri
                          </FormErrorMessage>
                        )}
                      </FormControl>

                      <FormControl
                        isRequired
                        isInvalid={
                          checkError("LASTNAME_REQ") ||
                          checkError("LASTNAME_MIN") ||
                          checkError("LASTNAME_MAX")
                        }
                      >
                        <FormLabel>Cognome</FormLabel>
                        <Input
                          value={lastname}
                          onChange={(e) => setLastname(e.target.value)}
                          type="text"
                        />
                        {checkError("LASTNAME_REQ") && (
                          <FormErrorMessage>
                            Il nome  obbligatorio
                          </FormErrorMessage>
                        )}
                        {checkError("LASTNAME_MIN") && (
                          <FormErrorMessage>
                            Occorrono almeno 2 caratteri
                          </FormErrorMessage>
                        )}
                        {checkError("LASTNAME_MAX") && (
                          <FormErrorMessage>
                            Troppo lungo, si prega di non superare 50 caratteri
                          </FormErrorMessage>
                        )}
                      </FormControl>

                      <FormControl
                        isRequired
                        isInvalid={
                          checkError("PHO_REQ") ||
                          checkError("PHO_MIN") ||
                          checkError("PHO_MAX")
                        }
                      >
                        <FormLabel>Numero di telefono</FormLabel>
                        <Input
                          value={phone}
                          onChange={(e) => setPhone(e.target.value)}
                          type="text"
                        />
                        {checkError("PHO_REQ") && (
                          <FormErrorMessage>
                            Il numero di telefono  obbligatorio
                          </FormErrorMessage>
                        )}
                        {(checkError("PHO_MIN") || checkError("PHO_MAX")) && (
                          <FormErrorMessage>
                            Lunghezza non valida, prego controllare il numero di
                            telefono
                          </FormErrorMessage>
                        )}
                      </FormControl>
                    </SimpleGrid>
                  </Box>

                  <Box className="menhir-input-form">
                    <Text as="h4" mt={0} mb={4} fontWeight="bold">
                      IL TUO INDIRIZZO
                    </Text>
                    <FormControl
                      id="address-street"
                      isRequired
                      isInvalid={
                        checkError("ADDR_REQ") ||
                        checkError("ADDR_MIN") ||
                        checkError("ADDR_MAX")
                      }
                    >
                      <FormLabel>Via</FormLabel>
                      <Input
                        value={address}
                        onChange={(e) => setAddress(e.target.value)}
                        type="text"
                      />
                      {checkError("ADDR_REQ") && (
                        <FormErrorMessage>
                          L'indirizzo  obbligatorio
                        </FormErrorMessage>
                      )}
                      {checkError("ADDR_MIN") && (
                        <FormErrorMessage>
                          Lunghezza non valida, almeno 10 caratteri
                        </FormErrorMessage>
                      )}
                      {checkError("ADDR_MAX") && (
                        <FormErrorMessage>
                          Troppo lungo, si prega di non superare i 100 caratteri
                        </FormErrorMessage>
                      )}
                    </FormControl>
                    <SimpleGrid columns={2} spacing={4} mt={4}>
                      <FormControl
                        isRequired
                        isInvalid={
                          checkError("CITY_REQ") ||
                          checkError("CITY_MIN") ||
                          checkError("CITY_MAX")
                        }
                      >
                        <FormLabel>Citt</FormLabel>
                        <Input
                          value={city}
                          onChange={(e) => setCity(e.target.value)}
                          type="text"
                        />
                      </FormControl>
                      <FormControl
                        isRequired
                        isInvalid={
                          checkError("ZIP_REQ") ||
                          checkError("ZIP_MIN") ||
                          checkError("ZIP_MAX")
                        }
                      >
                        <FormLabel>CAP</FormLabel>
                        <Input
                          value={zipcode}
                          onChange={(e) => setZipcode(e.target.value)}
                          type="text"
                        />
                        {checkError("ZIP_REQ") && (
                          <FormErrorMessage>
                            Il CAP  obbligatorio
                          </FormErrorMessage>
                        )}
                        {checkError("ZIP_MIN") && (
                          <FormErrorMessage>
                            Il cap deve essere di 5 caratteri
                          </FormErrorMessage>
                        )}
                        {checkError("ZIP_MAX") && (
                          <FormErrorMessage>
                            Il cap deve essere di 5 caratteri
                          </FormErrorMessage>
                        )}
                      </FormControl>
                      <Box>
                        <FormLabel>Regione</FormLabel>
                        <Select
                          placeholder="Scegli la regione"
                          value={selectedRegion}
                          onChange={regionChange}
                          isRequired
                          isInvalid={checkError("REGION_REQ")}
                        >
                          {plainRegions.map((reg, idx) => {
                            return (
                              <option key={idx} value={reg}>
                                {reg}
                              </option>
                            );
                          })}
                        </Select>
                        {checkError("REGION_REQ") && (
                          <FormErrorMessage>
                            La regione  obbligatoria
                          </FormErrorMessage>
                        )}
                      </Box>
                      <Box>
                        <FormLabel>Provincia</FormLabel>
                        <Select
                          value={selectedCounty}
                          onChange={countyChange}
                          isRequired
                          isInvalid={checkError("COUNTY_REQ")}
                        >
                          <option value="" key="empty-key" />
                          {counties.map((allCountries, idx) => {
                            return (
                              <option key={idx} value={allCountries}>
                                {allCountries}
                              </option>
                            );
                          })}
                        </Select>
                        {checkError("COUNTY_REQ") && (
                          <FormErrorMessage>
                            La provinia  obbligatoria
                          </FormErrorMessage>
                        )}
                      </Box>
                    </SimpleGrid>
                  </Box>
                </SimpleGrid>

                <Center mt={4}>
                  <Link to="/login">
                    <Button
                      mt={10}
                      mb={10}
                      backgroundColor="lightgray"
                      size="md"
                      fontWeight="normal"
                      mr={4}
                    >
                      INDIETRO
                    </Button>
                  </Link>
                  <Button
                    onClick={createAccount}
                    mt={10}
                    mb={10}
                    bg="brand.100"
                    color="#ffffff"
                    _hover={{ bgColor: "brand.100shade" }}
                    size="md"
                    fontWeight="normal"
                  >
                    CONFERMA
                  </Button>
                </Center>
              </Box>
            )}

            {viewState === 1 && (
              <Center>
                <Box
                  width={{ base: "100%", md: "25%" }}
                  p={{ base: 5, md: 4 }}
                  className="menhir-input-form"
                  boxShadow="md"
                  mt={{ base: 0, md: 6 }}
                >
                  <Text as="h4" mt={0} mb={4} fontWeight="bold">
                    TI ABBIAMO INVIATO UN CODICE VIA MAIL..
                  </Text>
                  <Text as="p" mt={2} mb={10}>
                    Per verificare il tuo account, abbiamo inviato un codice
                    nella tua casella di posta elettronica. Inserisci il codice
                    per completare la tua registrazione.
                  </Text>
                  <FormControl>
                    <FormLabel>Codice di Validazione</FormLabel>
                    <Input
                      value={authCode}
                      onChange={(e) => setAuthCode(e.target.value)}
                      type="text"
                    />
                  </FormControl>
                  <Center>
                    <Button
                      onClick={validateAccount}
                      mt={12}
                      mb={6}
                      bg="brand.100"
                      color="#ffffff"
                      _hover={{ bgColor: "brand.100shade" }}
                      size="md"
                      fontWeight="normal"
                    >
                      CONFERMA
                    </Button>
                  </Center>
                </Box>
              </Center>
            )}

            {viewState === 2 && (
              <Center>
                <Box
                  width={{ base: "100%", md: "25%" }}
                  p={{ base: 5, md: 4 }}
                  className="menhir-input-form"
                  boxShadow="md"
                  mt={{ base: 0, md: 6 }}
                >
                  <Text as="h4" mt={0} mb={4} fontWeight="bold">
                    ACCOUNT CONFERMATO
                  </Text>
                  <Text as="p" mt={2} mb={4}>
                    Grazie per aver registrato il tuo account. Tutto  pronto
                    per acquistare sul nostro ecommerce!
                  </Text>
                  <Center>
                    <Link to="/checkoutship">
                      <Button
                        mt={12}
                        mb={6}
                        bg="brand.100"
                        color="#ffffff"
                        _hover={{ bgColor: "brand.100shade" }}
                        size="md"
                        fontWeight="normal"
                      >
                        PROSEGUI
                      </Button>
                    </Link>
                  </Center>
                </Box>
              </Center>
            )}
          </Box>
        )}
      </Flex>
    </Box>
  );
}
