import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { cartActions, MenhirStoreState } from "./store/index";
import { useSelector } from "react-redux";
import "./Checkout.css";
import {
  Center,
  Box,
  Text,
  Image,
  SimpleGrid,
  Flex,
  Button,
} from "@chakra-ui/react";
import thanksImage from "./thanksImage.png";
import thanksImageSmall from "./thanksImage-small.png";
import { BiPackage } from "react-icons/bi";
import { FaTruck, FaClock } from "react-icons/fa";
import React, { useEffect, useState } from "react";
import axios from "axios";
import { Customer } from "./model/Customer";

export default function MenhirOrderCompleted(props: any) {
  const dispatch = useDispatch();
  const cart = useSelector((state: MenhirStoreState) => state.Cart);
  const [itemCount] = useState(cart.length);
  const [customer, setCustomer] = useState({});
  const shippingMethod = useSelector(
    (state: MenhirStoreState) => state.ShippingMethod
  );
  const username = useSelector((state: MenhirStoreState) => state.Username); 

  const getData = async () => {
    axios.defaults.withCredentials = true;
    axios(
      `${process.env.REACT_APP_BACKEND_URL}/customers?username=${username}`,
      {
        method: "get",
      }
    )
      .then((res) => {
        let customer: Customer = res.data;
        if (customer === null) throw new Error("Error");
        setCustomer(customer);
      })
      .catch((e) => {
        alert("error reading customer info");
      });
  };  

  useEffect(() => {    
    if (shippingMethod !== 0) {
      getData();
    }
    dispatch(cartActions.clear({}));
  }, []);  

  return (
    <Flex>
      <Box flex="1" p={0}>
        <Text as="h3" mt={{ base: 0, md: 5 }} mb={{ base: 0, md: 10 }}>
          IL TUO ORDINE E' CONFERMATO
        </Text>
        <Box className="menhir-input-form" mt={8} boxShadow="md">
          <SimpleGrid columns={3} minChildWidth="220px">
            <Box mt={2} mb={6}>
              <Center>
                <Image
                  display={{ base: "block", md: "none" }}
                  src={thanksImageSmall}
                  alt="Grazie per il tuo ordine"
                />
                <Image
                  display={{ base: "none", md: "block" }}
                  src={thanksImage}
                  alt="Grazie per il tuo ordine"
                />
              </Center>
            </Box>
            <Box mt={4}>
              <BiPackage style={{ display: "inline", color: "#4fc4ac" }} />
              <Text as="p" mt={2} mb={2} ml={2} style={{ display: "inline" }}>
                Il tuo ordine contiene
              </Text>
              <Text
                as="p"
                mt={2}
                mb={6}
                ml={6}
                fontSize="20px"
                fontWeight="bold"
              >
                {itemCount} prodotti
              </Text>
            </Box>

            {shippingMethod === 1 && (
              <Box mt={4}>
                <FaTruck style={{ display: "inline", color: "#4fc4ac" }} />
                <Text
                  as="p"
                  mt={2}
                  mb={2}
                  ml={2}
                  style={{ display: "inline" }}
                >
                  Il pacco sar inviato a:
                </Text>
                <Text
                  as="p"
                  mt={2}
                  mb={2}
                  ml={6}
                  fontSize="20px"
                  fontWeight="bold"
                >
                  {(customer as Customer).address}
                </Text>
                <Text as="p" mt={2} mb={2} ml={6} fontWeight="bold">
                {(customer as Customer).zipcode}&nbsp;{(customer as Customer).city}
                </Text>
                <Text as="p" mt={2} mb={6} ml={6} fontWeight="bold">
                {(customer as Customer).county}
                </Text>
              </Box>
            )}

            {shippingMethod === 0 && (
              <Box mt={4}>
                <FaTruck style={{ display: "inline", color: "#4fc4ac" }} />
                <Text
                  as="p"
                  mt={2}
                  mb={2}
                  ml={2}
                  style={{ display: "inline" }}
                >
                  Potrai ritirare l'ordine in negozio
                </Text>
                <Text
                  as="p"
                  mt={2}
                  mb={2}
                  ml={6}
                  fontSize="20px"
                  fontWeight="bold"
                >
                  Da domani mattina
                </Text>
              </Box>
            )}

            {shippingMethod === 1 && (
              <Box mt={4}>
                <FaClock style={{ display: "inline", color: "#4fc4ac" }} />
                <Text
                  as="p"
                  mt={2}
                  mb={2}
                  ml={2}
                  style={{ display: "inline" }}
                >
                  Consegna prevista in
                </Text>
                <Text
                  as="p"
                  mt={2}
                  mb={6}
                  ml={6}
                  fontSize="20px"
                  fontWeight="bold"
                >
                  3-5 giorni
                </Text>
              </Box>
            )}

            {shippingMethod === 0 && (
              <Box mt={4}>
                <FaClock style={{ display: "inline", color: "#4fc4ac" }} />
                <Text
                  as="p"
                  mt={2}
                  mb={2}
                  ml={2}
                  style={{ display: "inline" }}
                >
                  Pagamento
                </Text>
                <Text
                  as="p"
                  mt={2}
                  mb={6}
                  ml={6}
                  fontSize="20px"
                  fontWeight="bold"
                >
                  Pagherai in negozio al ritiro dell'ordine
                </Text>
              </Box>
            )}
          </SimpleGrid>
        </Box>

        <Center mt={4}>
          <Link to="/">
            <Button
              mt={10}
              mb={10}
              bg="brand.100"
              color="#ffffff"
              _hover={{ bgColor: "brand.100shade" }}
              size="md"
              fontWeight="normal"
            >
              CONTINUA GLI ACQUISTI
            </Button>
          </Link>
        </Center>
      </Box>
    </Flex>
  );
}
