import React from "react";
import { useDispatch } from "react-redux";
import { cartActions } from "./store/index";
import {
  Box,
  Badge,
  SimpleGrid,
  Text,
  Image,
  Spacer,
  FormLabel,
} from "@chakra-ui/react";
import { MenhirPriceTag } from "./Price";
import { MdClear } from "react-icons/md";
import { Product } from "./model/Product";

export default function MenhirCartItem(props: any) {
  const dispatch = useDispatch();
  const item = props.item;
  const imgFileName = "./demo-images/" + item.imgName.replace("<S>", "small");
  const qtyLabel = item.uom === "unit" ? " al pezzo" : "l'etto";
  console.log(item);
  const removeProduct = (code) => {
    const prod: Product = {
      code: code,
    };
    dispatch(cartActions.rem({ amount: 1, product: prod }));
  };

  return (
    <Box bgColor="whitesmoke" p={2} mb={2} display="flex" boxShadow="md">
      <Box>
        <Image
          maxH="100px"
          maxW="100px"
          mr={2}
          align="center"
          src={require(`${imgFileName}`).default}
        />
      </Box>
      <Box display="flex" flex="1">
        <Box flex="1">
          <Box display="flex" flex="1">
            <Box>
              <Text as="h4" mr={2} className="stay" fontWeight="bold">{item.title}</Text>
              <span>
                {item.discount > 0 && (
                  <Badge colorScheme="green" fontSize="1em" mt={1} mb={1}>
                    -{item.discount}%
                  </Badge>
                )}
              </span>
              <Box>
                <Text as="p" fontSize="sm">{item.description}</Text>
              </Box>
            </Box>
            <Spacer />
            <Box color="#666666" cursor="pointer">
              <Box display="flex" justifyContent="end">
                <MdClear
                  size="22px"
                  onClick={() => removeProduct(`${item.code}`)}
                />
              </Box>
            </Box>
          </Box>
          <Box display="flex" justifyContent="end">
            <SimpleGrid columns={{ base: 1, md: 3 }} mt={{base:0, md:3}}>
              <Box display="flex" justifyContent="end">
                <FormLabel mt={1} fontSize="11px">
                  Prezzo <br /><span>{qtyLabel}</span>
                </FormLabel>
                <Box ml={2}>
                <MenhirPriceTag
                  discountedPrice=""
                  price={item.price}
                  discount={item.discount}
                  showBadge={false}
                />
                </Box>
              </Box>
              <Box display="flex" justifyContent="end">
                <FormLabel ml={3} mr={1} mt={1} fontSize="11px">
                  Quantit:
                </FormLabel>
                <FormLabel ml={1} mr={2} mt={1} fontWeight="bold">
                  {item.quantity}
                </FormLabel>
              </Box>
              <Box display="flex" justifyContent="end">
                <FormLabel ml={4} mr={1} mt={1}>
                  Totale
                </FormLabel>
                <MenhirPriceTag
                  discountedPrice=""
                  price={item.priceTotal}
                  discount={0}
                />
              </Box>
            </SimpleGrid>
          </Box>
        </Box>
      </Box>
    </Box>
  );
}
