import React from "react";
import { useSelector } from "react-redux";
import { Box, Text, SimpleGrid, Divider } from "@chakra-ui/react";
import { MenhirPriceTag } from "./Price";
import { MenhirStoreState } from "./store";

export default function MenhirOrderSummary(props: any) {
  const cart = useSelector((state: MenhirStoreState) => state.Cart);
  const itemCount = cart.length;
  const totalItems = cart.reduce((acc, val) => {
    const discount = val.discount;
    const discountedPrice = val.price - (val.price * discount) / 100;
    return acc + (discountedPrice * val.quantity);
  }, 0);
  
  const shippingCost = parseFloat(`${process.env.REACT_APP_SHIPPING_COST}`);
  
  const shippingMethod: number = useSelector((state: MenhirStoreState) => state.ShippingMethod);
  const showShippingCost = shippingMethod === 1;
  
  let totalCart = 0;
  
  if (showShippingCost) {
    totalCart = totalItems + shippingCost;
  } else {
    totalCart = totalItems;
  }

  const productsLabel = totalItems > 1 ? " prodotti" : "prodotto";

  return (
    <Box>
      <Text as="h4" mt={0} mb={4} fontWeight="bold">
        RIEPILOGO ORDINE
      </Text>
      <SimpleGrid columns={2}>
        <Box>
          <Text as="h4">
            {itemCount} {productsLabel}
          </Text>
        </Box>
        <Box>
          <MenhirPriceTag price={totalItems} discount={0} />
        </Box>

        {showShippingCost && (
            <Box>
              <Text as="h4">Spedizione</Text>
            </Box>
        )}
        {showShippingCost && (
            <Box>
              <MenhirPriceTag price={shippingCost} discount={0} />
            </Box>
        )}

      </SimpleGrid>
      <Divider mt={2} style={{ borderColor: "#666666" }} />
      <SimpleGrid columns={2} mt={2}>
        <Box>
          <Text
            as="h4"
            color="teal.600"
            textTransform="uppercase"
            fontWeight="bold"
          >
            Totale
          </Text>
        </Box>
        <Box>
          <Text as="h4" mt={0}>
            <MenhirPriceTag price={totalCart} discount={0} />
          </Text>
        </Box>
      </SimpleGrid>
    </Box>
  );
}
