import React from "react";
import {
  Box,
  Image,
  Badge,
  Text,
  Flex,
  Center,
  Heading,
  Button,
  SimpleGrid,
  Divider,
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionIcon,
  FormLabel,
  AccordionPanel,
  NumberInput,
  NumberInputField,
  NumberInputStepper,
  NumberIncrementStepper,
  NumberDecrementStepper,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";
import "./MiniProduct.css";
import "./Product.css";
import MenhirWindow from "./Window";
import { FaStar, FaCartPlus } from "react-icons/fa";
import { useDispatch } from "react-redux";
import { Product } from "./model/Product";
import { cartActions } from "./store/index";
import { useToast } from "@chakra-ui/react";

const MenhirProduct = (props: any) => {
  const textGrayColor = "gray.600";
  const locProps = props.location.productProps;
  const imgPath = "./demo-images/" + locProps.imgName.replace("<S>", "big");
  const price = locProps.price;
  const discountedPrice = price - (price * locProps.discount) / 100;
  const iconPrimaryColor = "teal.500";
  const iconStar = "yellow.400";
  const iconGrayColor = "gray.400";
  const [value, setValue] = React.useState(1);
  const handleChange = (value: any) => {
    setValue(parseInt(value));
  };

  const dispatch = useDispatch();
  const toast = useToast();

  let prod: Product = {
    title: locProps.title,
    code: locProps.code,
    description: locProps.description,
    price: locProps.price,
    discount: locProps.discount,
    category: locProps.category,
    imgName: locProps.imgName,
    uom: locProps.uom,
    priceTotal: 0,
    quantity: 0,
  };

  const qtyLabel = locProps.uom === "hg" ? "in etti" : "al pezzo";

  const addProduct = () => {
    const newprod: Product = { ...prod };

    newprod.quantity = value;
    newprod.priceTotal =
      newprod.discount === 0
        ? newprod.quantity * newprod.price
        : discountedPrice;

    dispatch(cartActions.add({ amount: value, product: newprod }));
    const labelProd = value > 1 ? "prodotti aggiunti" : "prodotto aggiunto";
    toast({
      description: `${value} ${labelProd} al tuo carrello`,
      status: "success",
      duration: 3000,
      position: "top-left",
    });
  };

  return (
    <Box overflow="hidden">
      <MenhirWindow showHomeBanner={false}></MenhirWindow>
      <Box bgColor="gray.50" p={4}>
        <SimpleGrid mt={6} columns={[1, 2]}>
          <Box>
            <Breadcrumb fontSize="sm">
              <BreadcrumbItem>
                <BreadcrumbLink>
                  <Link to="/">Home</Link>
                </BreadcrumbLink>
              </BreadcrumbItem>
              <BreadcrumbItem>
                <Link
                  to={{
                    pathname: "/browser",
                    state: [
                      { catId: `${locProps.category}` },
                      { catDesc: `${locProps.categoryDescription}` },
                    ],
                  }}
                >
                  <BreadcrumbLink>
                    {locProps.categoryDescription}
                  </BreadcrumbLink>
                </Link>
              </BreadcrumbItem>
              <BreadcrumbItem isCurrentPage>
                <BreadcrumbLink href="#">{locProps.title}</BreadcrumbLink>
              </BreadcrumbItem>
            </Breadcrumb>
            <Center>
              <Image
                mt={8}
                mb={{ base: "6", md: "0" }}
                align="center"
                src={require(`${imgPath}`).default}
              />
            </Center>
          </Box>
          <Box>
            <Heading as="h1" fontSize={28} textTransform="uppercase">
              {locProps.title}
            </Heading>
            <Box d="flex" mt="2">
              {Array(5)
                .fill("")
                .map((_, i) => (
                  <Box
                    key={i}
                    color={i < locProps.rating ? iconStar : iconGrayColor}
                  >
                    <FaStar key={i} />
                  </Box>
                ))}
            </Box>
            <Flex mt={8}>
              <Box>
                <span>
                  {locProps.discount > 0 && (
                    <Badge colorScheme="green">-{locProps.discount}%</Badge>
                  )}
                </span>
              </Box>
              <Box ml={4}>
                {locProps.discount > 0 && (
                  <Text
                    fontSize="18"
                    style={{
                      textDecoration:
                        discountedPrice !== price ? "line-through" : "none",
                      marginRight: "5px",
                      color: "gray.800",
                    }}
                  >
                    {new Intl.NumberFormat("it-IT", {
                      style: "currency",
                      currency: "EUR",
                    }).format(locProps.price)}
                  </Text>
                )}
              </Box>
            </Flex>
            <Text fontSize={28} mt={1}>
              {discountedPrice !== price &&
                new Intl.NumberFormat("it-IT", {
                  style: "currency",
                  currency: "EUR",
                }).format(discountedPrice)}
              {locProps.discount === 0 &&
                new Intl.NumberFormat("it-IT", {
                  style: "currency",
                  currency: "EUR",
                }).format(locProps.price)}
            </Text>

            <Flex mt={8}>
              <Box>
                <FormLabel>Quantit {qtyLabel}</FormLabel>
                <NumberInput
                  maxW="100px"
                  mr="2rem"
                  value={value}
                  onChange={handleChange}
                  bgColor="white"
                  min={0}
                >
                  <NumberInputField />
                  <NumberInputStepper>
                    <NumberIncrementStepper />
                    <NumberDecrementStepper />
                  </NumberInputStepper>
                </NumberInput>
              </Box>

              <Button
                bg="brand.100"
                color="#ffffff"
                _hover={{ bgColor: "brand.100shade" }}
                size="md"
                fontWeight="normal"
                onClick={addProduct}
                mt={{ base: 0, md: 8 }}
              >
                <Box display={{ base: "none", md: "inline" }}>
                  <FaCartPlus size="24px" />
                </Box>
                &nbsp;&nbsp;AGGIUNGI AL CARRELLO
              </Button>
            </Flex>
          </Box>
        </SimpleGrid>
        <Divider mt={8} mb={8}></Divider>
        <SimpleGrid columns={[1, 2]}>
          <Text
            fontSize={{ base: "14", md: "16" }}
            color={textGrayColor}
            bgColor="white"
            textAlign="justify"
            size="xl"
            p={4}
            className="line-breaks"
            lineHeight="6"
            borderRadius={8}
          >
            {locProps.longDescription}
          </Text>
          <Box color={iconPrimaryColor} pl={2}>
            <Accordion allowMultiple outline={0}>
              <AccordionItem className="disable-aria">
                <h2>
                  <AccordionButton outline="0">
                    <Box flex="1" textAlign="left" outline="0">
                      CONSEGNA o RITIRO IN NEGOZIO
                    </Box>
                    <AccordionIcon />
                  </AccordionButton>
                </h2>
                <AccordionPanel pb={4} color="gray.600">
                  Consegnamo a casa tua o se preferisci puoi ordinare online e
                  passare a ritirare in negozio.
                </AccordionPanel>
              </AccordionItem>
              <AccordionItem>
                <h2>
                  <AccordionButton>
                    <Box flex="1" textAlign="left">
                      SERVIZIO CLIENTI
                    </Box>
                    <AccordionIcon />
                  </AccordionButton>
                </h2>
                <AccordionPanel pb={4} color="gray.600">
                  Un nostro addetto sar sempre a disposizione per consigliarti
                  e per aiutarti durante un acquisto.
                </AccordionPanel>
              </AccordionItem>
              <AccordionItem>
                <h2>
                  <AccordionButton>
                    <Box flex="1" textAlign="left">
                      PAGAMENTO
                    </Box>
                    <AccordionIcon />
                  </AccordionButton>
                </h2>
                <AccordionPanel pb={4} color="gray.600">
                  Offriamo le seguenti modalit di pagamento:
                  <li>Alla consenga</li>
                  <li>In negozio</li>
                  <li>Tramite carta di credito on line</li>
                </AccordionPanel>
              </AccordionItem>
            </Accordion>
          </Box>
        </SimpleGrid>
      </Box>
    </Box>
  );
};

export default MenhirProduct;
