import React from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { cartActions, MenhirStoreState } from "./store/index";
import {
  Center,
  Box,
  Text,
  SimpleGrid,
  Button,
} from "@chakra-ui/react";
import MenhirWindow from "./Window";
import { Product } from "./model/Product";
import { AiOutlineShoppingCart } from "react-icons/ai";
import MenhirOrderSummary from "./OrderSummary";
import CartItem from "./CartItem";

export default function MenhirCart(props:any) {
  const dispatch = useDispatch();
  const removeProduct = (code) => {
    const prod: Product = {
      Code: code,
    };
    dispatch(cartActions.rem({ amount: 1, product: prod }));
  };

  const cart = useSelector((state:MenhirStoreState) => state.Cart);

  return (
    <Box>
      <MenhirWindow showHomeBanner={false} />
      {cart && cart.length > 0 && (
        <Text as="h3" mt={{ base: 0, md: 10 }}>
          IL TUO CARRELLO
        </Text>
      )}

      {!cart ||
        (cart.length === 0 && (
          <Box>
            <Text as="h3" mt={{ base: 0, md: 10 }}>
              CARRELLO VUOTO
            </Text>
            <Center>
              <Box mr={3} color="teal.600">
                <AiOutlineShoppingCart size="40px" />
              </Box>
            </Center>
          </Box>
        ))}

      {cart && cart.length > 0 && (
        <SimpleGrid columns={2} spacing={10} minChildWidth="300px" p={4}>
          <Box>
            {cart.map((item, index) => (
              <CartItem key={index} item={item} />
            ))}
          </Box>
          <Box>
          <MenhirOrderSummary />
          <Center>
          <Link to="/checkoutship">
            <Button
              mt={10}
              mb={10}
              bg="brand.100"
              color="#ffffff"
              _hover={{ bgColor: "brand.100shade"}}
              size="md"
              fontWeight="normal"
            >
              VAI IN CASSA
            </Button>
          </Link>
        </Center>          
          </Box>
        </SimpleGrid>
      )}



      {!cart ||
        (cart.length === 0 && (
          <Center>
            <Link to="/browser">
              <Button
                mt={10}
                mb={10}
                bg="brand.100"
                color="#ffffff"
                _hover={{ bgColor: "brand.100shade"}}
                size="md"
                fontWeight="normal"
              >
                SFOGLIA I PRODOTTI
              </Button>
            </Link>
          </Center>
        ))}
    </Box>
  );
}
