import * as React from "react";
import { Text, Box, Badge } from "@chakra-ui/react";

export const MenhirPriceTag = (props: any) => {
  const price = props.price;
  const discount = props.discount;
  const showBadge = props.showBadge;
  const discountedPrice = price - (price * discount) / 100;

  return (
    <Box display="flex" flexDir="row">
      { discount > 0 && showBadge &&
      <Box mr={2}>
        <span>
          {discount > 0 && (
            <Badge colorScheme="green" fontSize="1em" mt={1} mb={1}>-{discount}%</Badge>
          )}
        </span>
      </Box>
      }
      <Text
        mt={1}
        mr={2}
        style={{
          textDecoration: discountedPrice !== price ? "line-through" : "none",
          fontSize:  discountedPrice !== price ? "13px" : "18px",
          fontWeight:  discountedPrice !== price ? "normal" : "bold",
        }}
      >
        {new Intl.NumberFormat("it-IT", {
          style: "currency",
          currency: "EUR",
        }).format(price)}
      </Text>
      {discount > 0 && (
        <Text fontSize={18} mt={1} style={{ fontWeight:  discountedPrice === price ? "normal" : "bold" }}>
          {discountedPrice !== price &&
            new Intl.NumberFormat("it-IT", {
              style: "currency",
              currency: "EUR",
            }).format(discountedPrice)}
          {discount === 0 &&
            new Intl.NumberFormat("it-IT", {
              style: "currency",
              currency: "EUR",
            }).format(price)}
        </Text>
      )}
    </Box>
  );
};
