export default function MenhirCheckoutShip(props: any) {
  const dispatch = useDispatch();
  axios.defaults.withCredentials = true;
  const cart = useSelector((state: MenhirStoreState) => state.Cart);
  const isAuth = useSelector(
    (state: MenhirStoreState) => state.IsAuthenticated
  );
  const username = useSelector((state: MenhirStoreState) => state.Username);
  const [regionData, setRegionData] = useState([]);
  const [shippingMethod, setshippingMethod] = useState("0");
  const [selectedRegion, setSelectedRegion] = useState("");
  const [selectedCounty, setSelectedCounty] = useState("");
  const [customer, setCustomer] = useState({});
  const [counties, setCounties] = useState([]);

  const getSupportedShippingMehotds = (methodsParam: string | undefined) => {
    let methodList = ["RITIRO IN NEGOZIO"];
    if (!methodsParam) return methodList;
    const methods: string[] = methodsParam.split(",");
    if (methods.find((e) => e === "1")) {
      methodList.push("CONSEGNA A CASA");
    }
    return methodList;
  };

  const shippingMethods = getSupportedShippingMehotds(
    `${process.env.REACT_APP_SUPPORTED_SHIPPING_METHODS}`
  );  

  const getData = async () => {
    axios.defaults.withCredentials = true;
    axios(
      `${process.env.REACT_APP_BACKEND_URL}/customers?username=${username}`,
      {
        method: "get",
      }
    )
      .then((res) => {
        let customer: Customer = res.data;
        if (customer === null) throw new Error("Error");
        setCustomer(customer);
        for (const property in regions) {
          if (property === customer.region) setCounties(regions[property]);
        }
      })
      .catch((e) => {
        alert("error reading customer info");
      });
  };

  const checkInsertInArray = (newRegion) => {
    let findStatus = regionData.find((x) => {
      return x === newRegion;
    });
    if (!findStatus) {
      const list = [...regionData, newRegion];
      setRegionData(list.sort());
    }
  };

  const regionChange = (event) => {
    if (event.target.value) {
      setSelectedRegion(event.target.value);
      setCounties(regions[event.target.value]);
    }
  };

  const countyChange = (event) => {
    if (event.target.value) {
      setSelectedCounty(event.target.value);
    }
  };

  const setShipping = () => {
    dispatch(
      cartActions.setShippingMethod({
        shippingMethod: parseInt(shippingMethod),
      })
    );
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <Flex>
      <Box flex="1" p={0}>
        {cart && cart.length > 0 && (
          <Text as="h3" mt={{ base: 0, md: 5 }} mb={{ base: 0, md: 10 }}>
            CASSA / INDIRIZZO DI CONSEGNA
          </Text>
        )}

        {cart && cart.length > 0 && (
          <SimpleGrid columns={2} spacing={5} minChildWidth="300px" p={2}>
            <Box className="menhir-input-form">
              <Text as="h4" mt={0} mb={4} fontWeight="bold">
                DATI DEL TUO ACCOUNT
              </Text>
              <SimpleGrid columns={2} spacing={4} mt={4}>
                <FormControl>
                  <FormLabel>Nome</FormLabel>
                  <Input value={customer.name} type="text" />
                </FormControl>
                <FormControl>
                  <FormLabel>Cognome</FormLabel>
                  <Input value={customer.lastname} type="text" />
                </FormControl>
                <FormControl>
                  <FormLabel>Numero di telefono</FormLabel>
                  <Input value={customer.phone} type="text" />
                </FormControl>
              </SimpleGrid>
            </Box>

            <Box className="menhir-input-form">
              <Text as="h4" mt={0} mb={4} fontWeight="bold">
                CONSEGNA o RITIRO IN NEGOZIO
              </Text>
              <Box>
                <RadioGroup
                  defaultValue="0"
                  onChange={setshippingMethod}
                  value={shippingMethod}
                >
                  <Stack>
                    {shippingMethods.map((el, idx) => (
                      <Radio
                        size="md"
                        value={idx.toString()}
                        colorScheme="teal"
                      >
                        <Box display="inline">{el}</Box>
                      </Radio>
                    ))}
                  </Stack>
                </RadioGroup>
              </Box>

              <Box mt={4} display="none">
                <Text as="h4" mt={0} mb={4} fontWeight="bold">
                  IL TUO INDIRIZZO PER LA CONSEGNA
                </Text>
                <FormControl id="address-street">
                  <FormLabel>Via</FormLabel>
                  <Input value={customer.address} type="text" />
                </FormControl>
                <SimpleGrid columns={2} spacing={4} mt={4}>
                  <FormControl>
                    <FormLabel>Citt</FormLabel>
                    <Input value={customer.city} type="text" />
                  </FormControl>
                  <FormControl>
                    <FormLabel>CAP</FormLabel>
                    <Input value={customer.zipcode} type="text" />
                  </FormControl>
                  <Box>
                    <FormLabel>Regione</FormLabel>
                    <Select
                      placeholder="Scegli la regione"
                      value={customer.region}
                      onChange={regionChange}
                    >
                      {plainRegions.map((reg) => {
                        return <option value={reg}>{reg}</option>;
                      })}
                    </Select>
                  </Box>
                  <Box>
                    <FormLabel>Provincia</FormLabel>

                    <Select value={customer.county} onChange={countyChange}>
                      <option value="" />
                      {counties.map((allCountries) => {
                        return (
                          <option value={allCountries}>{allCountries}</option>
                        );
                      })}
                    </Select>
                  </Box>
                </SimpleGrid>
              </Box>
            </Box>
          </SimpleGrid>
        )}

        <Center mt={4}>
          <Link to="/cart">
            <Button
              mt={10}
              mb={10}
              backgroundColor="lightgray"
              size="md"
              fontWeight="normal"
              mr={4}
            >
              INDIETRO
            </Button>
          </Link>
          <Link to="/checkoutpay">
            <Button
              onClick={setShipping}
              mt={10}
              mb={10}
              bg="brand.100"
              color="#ffffff"
              _hover={{ bgColor: "brand.100shade" }}
              size="md"
              fontWeight="normal"
            >
              CONFERMA
            </Button>
          </Link>
        </Center>
      </Box>
    </Flex>
  );
}
