import { ReactNode } from "react";
import logo from "./logo.png";
import {
  Box,
  HStack,
  Button,
  Divider,
  SimpleGrid,
  List,
  ListItem,
  ListIcon,
  Heading,
  Textarea,
  Text,
  Center,
  Image,
} from "@chakra-ui/react";
import imgHelp from "./help.PNG";
import { MdCheckCircle } from "react-icons/md";

const Logo = (props: any) => {
  return <Image src={logo} alt="React Menhir Shop" />;
};

export default function MenhirAbout() {
  let value = "";
  return (
    <Box>
      {/* <Box
        h="40px"
        bg="white"
        bgImage={imgAbout}
        bgPosition="center"
        bgRepeat="no-repeat"
        bgSize="1800 360px"
      ></Box> */}
      <Box pl={6} pt={4}>
        <SimpleGrid columns={2} spacingX="40px" spacingY="20px">
          <Box>
            <Heading color="gray.600" mt={4} as="h1" fontSize={24}>
              Conosciamo il nostro mestiere, e soprattutto i nostri prodotti
            </Heading>
            <Text mt={4}>
              Al primo posto per noi ci sono i prodotti che vendiamo, e che arrivano sulla
              tua tavola. Ogni boccone di un delizioso formaggio, o un prelibato salume, devono
              portare a casa tua il piacere di gustare prodotti di alta qualit.
            </Text>
            <Text mt={4}>
              La nostra missione  selezionare i migliori produttori, ricercare le prelibatezze della
              nostra tradizione, e metterli a disposizione della nostra clientela, e aiutare a costruire
              quel clima caloroso e accogliente tipico delle cucine italiane.
            </Text>
          </Box>
          <Box>
            <Heading color="gray.600" mt={4} mb={4} as="h1" fontSize={24}>
              Servizi
            </Heading>
            <List spacing={3}>
              <ListItem>
                <ListIcon as={MdCheckCircle} color="green.500" />Servizio Clienti
              </ListItem>
              <ListItem>
                <ListIcon as={MdCheckCircle} color="green.500" />
                Distribuiamo in Italia e all'estero
              </ListItem>
              <ListItem>
                <ListIcon as={MdCheckCircle} color="green.500" />
                Gestione dell'ordine in tempo reale
              </ListItem>
              <ListItem>
                <ListIcon as={MdCheckCircle} color="green.500" />
                Un esperto  a disposizione per consigliarti nella scelta dei nostri prodotti
              </ListItem>
            </List>
          </Box>
        </SimpleGrid>
        <Divider mt={8} mb={6}></Divider>
        <Center>
          <HStack spacing="50px" mt={10} mb={10}>
            <Box>
              <Image src={imgHelp} alt="Write us!" />
            </Box>
            <Box>
              <Heading color="teal.400" mt={4} mb={4} as="h2" fontSize={24}>
                Vuoi entrare in contatto? Scrivici due righe.
              </Heading>
              <Textarea
                maxW={{ base: "230px", md: "600px" }}
                value={value}
                placeholder="Hello!"
                size="sm"
                mt={4}
              />
              <Button bg="brand.100"
                color="#ffffff"
                _hover={{ bgColor: "brand.100shade"}}  color="#ffffff" size="lg" mt={4}>
                Invia
              </Button>
            </Box>
          </HStack>
        </Center>
      </Box>
    </Box>
  );
}
