import imgWelcome from "./home-landscape.png";
import imgWelcomePromo from "./home-landscape-promo.png";
import imgWelcomeBread from "./home-landscape-pane.png";

import imgWelcomeSmall from "./home-landscape-small.png";
import imgWelcomeSmallTwo from "./home-landscape-small-2.png";
import imgWelcomeSmallThree from "./home-landscape-small-3.png";

import { Box, HStack, Text } from "@chakra-ui/react";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Slider from "react-slick";
import axios from 'axios';
import "./slick.css";
import "./slick-theme.css";

const MenhirWindow = (props: any) => {
  const [categories, setCategories] = useState([]);

  const getCategories = () => {
    axios.get(`${process.env.REACT_APP_BACKEND_URL}/categories`).then((res) => {
      let prods = res.data;
      if (prods === null) return;
      setCategories(prods);
    });
  };

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  const randomArrayShuffle = (array: string[]) => {
    var currentIndex = array.length, temporaryValue, randomIndex;
    while (0 !== currentIndex) {
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
    return array;
  }  

  let smallImages = [imgWelcomeSmall, imgWelcomeSmallTwo, imgWelcomeSmallThree];
  let largeImages = [imgWelcome, imgWelcomePromo, imgWelcomeBread];

  smallImages = randomArrayShuffle(smallImages);
  largeImages = randomArrayShuffle(largeImages);

  useEffect(() => {
    getCategories();
  }, []);

  return (
    <Box>
      <Box
        bg="white"
        w="100%"
        borderColor="gray.200"
        display={{ base: "none", lg: "flex" }}
        flexDir={{ base: "column-reverse", md: "row" }}
        justifyContent="center"
        color="teal.600"
        borderBottom="1px"
        borderBottomColor="teal.600"
      >
        <HStack spacing="xs" bg="white" textAlign="center" mt="3" mb="4">
          {categories.map((item, index) => (
            <Box
              key={index}
              w="xs"
              h={5}
              pb={10}
              style={{ textTransform: "uppercase", cursor: "pointer" }}
              display="inline-block"
            >
              <Link
                key={index}
                to={{
                  pathname: "/browser",
                  state: [
                    { catId: `${item.code}` },
                    { catDesc: `${item.title}` },
                  ],
                }}
              >
                <Text
                  _hover={{ fontWeight: "bold" }}
                  as="h4"
                  mr={6}
                  fontSize="md"
                >
                  {item.title}
                </Text>
              </Link>
            </Box>
          ))}
        </HStack>
      </Box>
      {props.showHomeBanner && (
        <Slider {...settings}>
          <Box
            h="300px"
            bg="white"
            bgImage={{
              base: smallImages[0],
              md: smallImages[0],
              lg: largeImages[0],
            }}
            bgPosition="center"
            bgRepeat="no-repeat"
            bgSize="1800 360px"
          ></Box>
          <Box
            h="300px"
            bg="white"
            bgImage={{
              base: smallImages[1],
              md: smallImages[1],
              lg: largeImages[1],
            }}
            bgPosition="center"
            bgRepeat="no-repeat"
            bgSize="1800 360px"
          ></Box>
          <Box
            h="300px"
            bg="white"
            bgImage={{
              base: smallImages[2],
              md: smallImages[2],
              lg: largeImages[2],
            }}
            bgPosition="center"
            bgRepeat="no-repeat"
            bgSize="1800 360px"
          ></Box>          
        </Slider>
      )}
    </Box>
  );
};

export default MenhirWindow;
