import {
  Box,
  IconButton,
  Stack,
  Drawer,
  DrawerContent,
  DrawerHeader,
  DrawerOverlay,
  DrawerBody,
  useDisclosure,
  Image,
  Input,
  Text,
  InputGroup,
  CloseButton,
  Button,
  Center,
  Spacer,
  InputLeftElement,
  VStack,
} from "@chakra-ui/react";
import { HamburgerIcon, CloseIcon, SearchIcon } from "@chakra-ui/icons";
import { AiOutlineUser, AiOutlineShoppingCart } from "react-icons/ai";
import { BsCircleFill } from "react-icons/bs";
import axios from "axios";
import { Link } from "react-router-dom";
import logo from "./logo.png";
import logoSmall from "./logo-small.png";
import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import "./Navigation.css";
import { MenhirStoreState } from "./store";

export default function MenhirNavigation(props: any) {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const [categories, setCategories] = useState([]);

  const cart = useSelector((state:MenhirStoreState) => state.Cart);
  const isAuthenticated = useSelector((state:MenhirStoreState) => state.IsAuthenticated);
  const username = useSelector((state:MenhirStoreState) => state.Username);
  let items: number = parseInt(cart.length);

  const cartLabel: string =
    items === 0
      ? "IL TUO CARRELLO E' VUOTO"
      : `VEDI CARRELLO (${items.toString()})`;

  const getCategories = () => {
    axios.get(`${process.env.REACT_APP_BACKEND_URL}/categories`).then((res) => {
      let prods = res.data;
      if (prods === null) return;
      setCategories(prods);
    });
  };

  const handleOpenDrawer = () => {
    onOpen();
  };

  const handleCloseDrawer = () => {
    onClose();
  };

  useEffect(() => {
    getCategories();
  }, []);

  return (
    <Box
      bg="white"
      display="flex"
      justifyContent="space-between"
      className="nav-sticky menhir-nav-bar"
      mt="130px"
    >
      <Box w={{ base: "xs", md: "xs" }}>
        <IconButton
          display={{ base: "inline", lg: "none" }}
          onClick={handleOpenDrawer}
          icon={
            isOpen ? (
              <CloseIcon w={3} h={3} />
            ) : (
              <HamburgerIcon w={5} h={5} mb={1} />
            )
          }
          variant={"ghost"}
          aria-label={"Toggle Navigation"}
        />
        <Drawer onClose={onClose} isOpen={isOpen} size="full">
          <DrawerOverlay />
          <DrawerContent>
            <DrawerHeader>
              <Box display="flex" alignContent="space-between">
                <Text as="h3" mt={1}>
                  CERCA PRODOTTI
                </Text>
                <Spacer />
                <CloseButton onClick={handleCloseDrawer} />
              </Box>
            </DrawerHeader>
            <DrawerBody>
              <VStack spacing="xs" bg="white" textAlign="center" mt="3" mb="4">
                {categories.map((item, index) => (
                  <Box
                    key={index}
                    w="xs"
                    h={5}
                    pb={10}
                    style={{ textTransform: "uppercase", cursor: "pointer" }}
                    display="inline-block"
                  >
                    <Link
                      key={index}
                      onClick={handleCloseDrawer}
                      to={{
                        pathname: "/browser",
                        state: [
                          { catId: `${item.code}` },
                          { catDesc: `${item.title}` },
                        ],
                      }}
                      mr={6}
                      fontSize="md"
                      _active={{ color: "brand.100" }}
                    >
                      {item.title}
                    </Link>
                  </Box>
                ))}
              </VStack>
            </DrawerBody>
          </DrawerContent>
        </Drawer>
      </Box>

      <Box w="lg" alignContent="center">
        <Center>
          <Link to="/">
            <Image
              display={{ base: "block", md: "none" }}
              mt={{ base: "2", md: "0" }}
              src={logoSmall}
              w="280"
              alt="Il Tagliere di Mario"
              className={
                props.navBarScrolled ? "size-up grow" : "size-original grow"
              }
            />
            <Image
              display={{ base: "none", md: "block" }}
              src={logo}
              w="348"
              alt="Il Tagliere di Mario"
              className={
                props.navBarScrolled ? "size-up grow" : "size-original grow"
              }
            />
          </Link>
        </Center>
      </Box>
      <Box w="xs" display="flex" justifyContent="flex-end" pr={1}>
        <Link to="/cart">
          <Box className="nav-bar-end-elem" pr={{ base: "4", md: "0" }} pt={1}>
            {items == 0 && (
              <Text
                display={{ base: "none", md: "block" }}
                as="span"
                className="stay"
                mt="8px"
              >
                {cartLabel}&nbsp;&nbsp;
                <AiOutlineShoppingCart size="22px" className="stay" />
              </Text>
            )}

            {items > 0 && (
              <Box display={{ base: "inline-block", md: "none" }}>
                <AiOutlineShoppingCart size="22px" className="stay" />
                <Box className="stay" color="orange.500" pb="10px">
                  <BsCircleFill size="6px" className="stay" />
                </Box>
              </Box>
            )}

            {items > 0 && (
              <Button
                bg="brand.300"
                fontSize={{ base: "xs" }}
                display={{ base: "none", md: "inline-block" }}
                className="stay"
              >
                {cartLabel}
                <AiOutlineShoppingCart size="22px" className="stay" />
              </Button>
            )}
          </Box>
        </Link>
        <Box
          className="nav-bar-end-elem"
          display={{ base: "none", md: "inline-block" }}
          pt={1}
          mt="5px"
        >
          <Text
            as="span"
            display={{ base: "none", md: "inline-block" }}
            className="stay"
          >
            {username === "" && <span>Accedi</span>}
            {username !== "" && <span>{username}</span>}
          </Text>
          <AiOutlineUser size="22px" className="stay" />
        </Box>
      </Box>
    </Box>
  );
}

const SearchStack = () => {
  return (
    <Stack direction={"row"}>
      <InputGroup>
        <InputLeftElement
          pointerEvents="none"
          children={<SearchIcon color="gray.300" />}
        />
        <Input placeholder="Search" />
      </InputGroup>
    </Stack>
  );
};
