import React, { useState, useEffect } from "react";
import {
  Box,
  Stack,
  HStack,
  Text,
  Flex,
  useMediaQuery,
} from "@chakra-ui/react";
import MenhirMiniProduct from "./MiniProduct";
import { Link } from "react-router-dom";
import "./CategoryPreview.css";
import axios from "axios";
import MenhirPageBusy, { BusyProperties } from "./PageBusy";
import { Product } from "./model/Product";


const CheckMobile = () => {
  const [isLargerThan220] = useMediaQuery("(min-width: 380px)");
  console.log(".." + isLargerThan220);
  return isLargerThan220 ? 4 : 2;
};

const MenhirCategoryPreview = (props: any) => {
  const [howMany, sethowMany] = useState(CheckMobile());
  const [data, setData] = useState<Array<Product>>([]);
  const [busy, setBusy] = useState(false);

  const getData = () => {
    setBusy(true);
    axios.get(`${process.env.REACT_APP_BACKEND_URL}/products`).then((res) => {
      let prods = res.data;
      if (prods === null) return;
      prods = prods.filter((k, i) => k.category === props.category);
      setData(prods.sort(() => Math.random() - 0.5));
      setBusy(false);
    });
  };

  useEffect(() => {
    getData();
  }, []);

  const busyProps: BusyProperties = {
    Size: "small",
    HideText: true,
  };

  return (
    <Box
      bg="white"
      w="100%"
      mt={{ base: 5, md: 10 }}
      mb={10}
      borderColor="gray.200"
      display="flex"
      justifyContent="center"
    >
      {busy && <MenhirPageBusy {...busyProps} />}
      {!busy && (
        <Stack spacing={3} padding={2}>
          <Flex justifyContent="space-between">
            <Text
              as="h4"
              mb={2}
              ml={2}
              textTransform="uppercase"
              color="teal.600"
              fontSize="md"
              textAlign="center"
            >
              {props.title}
            </Text>
            <Link
              to={{
                pathname: "/browser",
                state: [{ catId: `ALL` }, { catDesc: `ALL` }],
              }}
              className="see-all-link"
              fontSize="sm"
              color="teal.600"
            >
              <Text mr={2} as="p">
                Vedi tutti
              </Text>
            </Link>
          </Flex>
          <HStack>
            {data.map(
              (item, index) =>
                index < howMany && (
                  <MenhirMiniProduct
                    key={index}
                    code={item.code}
                    title={item.title}
                    description={item.description}
                    discount={item.discount}
                    price={item.price}
                    rating={item.rating}
                    imgName={item.imgName}
                    category={item.category}
                    categoryDescription={props.title}
                    minimalView={true}
                    longDescription={item.longDescription}
                    uom={item.uom}
                  ></MenhirMiniProduct>
                )
            )}
          </HStack>
        </Stack>
      )}
    </Box>
  );
};

export default MenhirCategoryPreview;
