import React from "react";
import {
  Box,
} from "@chakra-ui/react";
import MenhirWindow from "./Window";
import MenhirCategoryPreview from "./CategoryPreview";

const randomCatFirst = Math.floor(Math.random() * 4) + 1;
let randomCatSecond = Math.floor(Math.random() * 4) + 1;

while (randomCatFirst === randomCatSecond) {
  randomCatSecond = Math.floor(Math.random() * 4) + 1;
}

const catLabels = ["Formaggi","Salumi","Pronti in tavola","Gourmandise"];

const getLabel = (idx: number) => {
  return catLabels[idx-1];
}

export default function MenhirHome() {
  return (
    <Box>
      <Box>
        <MenhirWindow showHomeBanner={true} />
      </Box>
      <MenhirCategoryPreview
        category={randomCatFirst}
        title={getLabel(randomCatFirst)}
      ></MenhirCategoryPreview>
      <MenhirCategoryPreview
        category={randomCatSecond}
        title={getLabel(randomCatSecond)}
      ></MenhirCategoryPreview>
    </Box>
  );
}
