import React, { useState } from "react";
import { Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { Route, Redirect, useLocation } from "react-router-dom";
import {
  Box,
  Button,
  Text,
  Center,
  FormControl,
  FormLabel,
  Input,
  FormErrorMessage,
  Alert,
  AlertIcon,
  AlertTitle,
  AlertDescription,
} from "@chakra-ui/react";
import MenhirWindow from "./Window";
import { cartActions } from "./store/index";
import axios from "axios";
import * as Yup from "yup";
import MenhirPageBusy, { BusyProperties } from "./PageBusy";

const loginValidationSchema = Yup.object().shape({
  email: Yup.string().required("EMAIL_REQ"),
  password: Yup.string().required("PSW_REQ"),
});

const miniAuth = {
  isAuthenticated: false,
  async authenticate(username: string, password: string): Promise<boolean> {
    axios.defaults.withCredentials = true;
    const cred = JSON.stringify({ Username: username, Password: password });
    return await axios
      .post(`${process.env.REACT_APP_BACKEND_URL}/login`, cred)
      .then((r) => {
        this.isAuthenticated = true;
        return true;
      })
      .catch((e) => {
        this.isAuthenticated = false;
        return false;
      });
  },
  async signout() {
    this.isAuthenticated = false;
  },
};

export default function MenhirLogin() {
  const [busy, setBusy] = useState(false);
  const [viewError, setViewError] = useState(false);
  const [formErrors, setFormErrors] = useState([]);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [redirectToReferrer, setRedirectToReferrer] = React.useState(false);
  const { state } = useLocation();
  const dispatch = useDispatch();

  const checkError = (errCode: string): boolean => {
    const err = formErrors.find((e) => e === errCode);
    return err !== null && err !== undefined;
  };

  const busyProps: BusyProperties = {
    Size: "large",
  };

  const login = () =>
    loginValidationSchema
      .validate(
        {
          email: username,
          password: password,
        },
        { abortEarly: false }
      )
      .then((res) => {
        if (res) {
          setBusy(true);
          miniAuth
            .authenticate(username, password)
            .then((r) => {
              if (!r) {
                setViewError(true);
                return;
              }
              dispatch(
                cartActions.login({ isAuthenticated: true, username: username })
              );
              setRedirectToReferrer(true);
            })
            .catch((e) => {
              setViewError(true);
            });
        }
      })
      .catch((err) => {
        console.log(err.name);
        console.log(err.errors);
        setFormErrors(err.errors);
      })
      .finally(() => {
        setBusy(false);
      });

  if (redirectToReferrer === true) {
    return <Redirect to={state?.from || "/"} />;
  }

  return (
    <Box flex="1" p={0}>
      <MenhirWindow showHomeBanner={false} />
      
      { viewError && (
      <Alert status="error" mt={4}>
        <AlertIcon />
        <AlertTitle mr={2}>Credenziali non valide.</AlertTitle>
        <AlertDescription>
          Per cortesia controlla di aver inserito la tua email e la password
          corrette.
        </AlertDescription>
      </Alert>
      )}
      <Center>
        {busy && <MenhirPageBusy {...busyProps} />}

        {!busy && (
          <Box
            width={{ base: "100%", md: "25%" }}
            p={{ base: 5, md: 4 }}
            className="menhir-input-form"
            boxShadow="md"
            mt={{ base: 0, md: 8 }}
          >
            <Box>
              <Text as="h3" mt={{ base: 0, md: 3 }}>
                ACCEDI AL TUO ACCOUNT
              </Text>
            </Box>
            <form>
              <FormControl
                id="username"
                isRequired
                isInvalid={checkError("EMAIL_REQ")}
              >
                <FormLabel>Indirizzo Email</FormLabel>
                <Input
                  type="email"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                />
                {checkError("EMAIL_REQ") && (
                  <FormErrorMessage>
                    Questo campo  obbligatorio
                  </FormErrorMessage>
                )}
              </FormControl>
              <FormControl
                id="password"
                mt={4}
                isRequired
                isInvalid={checkError("PSW_REQ")}
              >
                <FormLabel>Password</FormLabel>
                <Input
                  type="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
                {checkError("PSW_REQ") && (
                  <FormErrorMessage>
                    Questo campo  obbligatorio
                  </FormErrorMessage>
                )}
              </FormControl>

              <Box display="flex" justifyContent="space-between" mt={8}>
                <Link to="/account">
                  <Button
                    bg="brand.300"
                    color="#ffffff"
                    size="md"
                    mt={4}
                    type="submit"
                  >
                    <Text as="span" className="stay" color="brand.900">
                      Crea nuovo account
                    </Text>
                  </Button>
                </Link>
                <Button
                  onClick={login}
                  bg="brand.100"
                  color="#ffffff"
                  _hover={{ bgColor: "brand.100shade" }}
                  size="md"
                  mt={4}
                >
                  Login
                </Button>
              </Box>
            </form>
          </Box>
        )}
      </Center>
    </Box>
  );
}

export function MenhirPrivateRoute({ children, ...rest }) {

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return miniAuth.isAuthenticated === true ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: "/login",
              state: { from: location },
            }}
          />
        );
      }}
    />
  );
}
