import React from "react";
import { useDispatch } from "react-redux";
import { Link, useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import creditcard from "./credit-card-image-small.png";
import paypal from "./paypal-image-small.png";
import payinstore from "./payinstore-image-small.png";

import {
  Center,
  Stack,
  Radio,
  Box,
  Image,
  Text,
  SimpleGrid,
  Button,
  RadioGroup,
  Flex,
} from "@chakra-ui/react";
import MenhirOrderSummary from "./OrderSummary";
import { MenhirStoreState } from "./store";
import { OrderItem, TransportOrder } from "./model/TransportOrder";
import axios from "axios";
import { cartActions } from "./store/index";

export default function MenhirCheckoutPay(props) {
  const history = useHistory();
  const dispatch = useDispatch();
  const cart = useSelector((state: MenhirStoreState) => state.Cart);
  const username = useSelector((state: MenhirStoreState) => state.Username);
  const shippingMethod = useSelector(
    (state: MenhirStoreState) => state.ShippingMethod
  );
  const [paymentMethod, setPaymentMethod] = React.useState("0");
  const totalItems = cart.reduce((acc, val) => {
    const discount = val.discount;
    const discountedPrice = val.price - (val.price * discount) / 100;
    return acc + discountedPrice * val.quantity;
  }, 0);

  // if shippingmethod == 0, shipping cost must not be applied
  const shippingCost =
    shippingMethod === 0
      ? 0
      : parseFloat(`${process.env.REACT_APP_SHIPPING_COST}`);

  const totalCart = totalItems + shippingCost;

  const getSupportedPaymentMethods = (methodsParam: string | undefined) => {
    let methodList = [];
    if (!methodsParam) return methodList;
    const methods: string[] = methodsParam.split(",");
    if (methods.find((e) => e === "0") && shippingMethod === 0) {
      methodList.push("PAGAMENTO IN NEGOZIO");
    }    
    if (methods.find((e) => e === "1")) {
      methodList.push("CARTA DI CREDITO");
    }
    if (methods.find((e) => e === "2")) {
      methodList.push("PAYPAL");
    }
    return methodList;
  };

  const paymentMethods = getSupportedPaymentMethods(
    `${process.env.REACT_APP_SUPPORTED_PAYMENTS_METHODS}`
  );

  const placeOrder = () => {
    dispatch(
      cartActions.setPaymentMethod({ paymentMethod: parseInt(paymentMethod) })
    );

    let items: OrderItem[] = cart.map((item) => {
      return {
        code: item.code,
        title: item.title,
        quantity: item.quantity,
        price: item.priceTotal,
        discount: item.discount,
      };
    });

    const container: TransportOrder = {
      order: {
        email: username,
        paid: false,
        shipped: false,
        total: totalCart,
        paymentMethod: parseInt(paymentMethod),
        shippingMethod: shippingMethod,
      },
      items: items,
    };

    axios(`${process.env.REACT_APP_BACKEND_URL}/orders`, {
      data: container,
      withCredentials: true,
      method: "put",
    })
      .then((res) => {
        history.push("/ordercompleted");
      })
      .catch((err) => {
        alert(`Error placing order ${err}`);
      });
  };

  return (
    <Flex>
      <Box flex="1" p={0}>
        {cart && cart.length > 0 && (
          <Box>
            <Text as="h3" mt={{ base: 0, md: 5 }} mb={{ base: 0, md: 10 }}>
              CHECKOUT / PAY
            </Text>
          </Box>
        )}

        {cart && cart.length > 0 && (
          <SimpleGrid columns={2} spacing={5} minChildWidth="300px" p={2}>
            <Box className="menhir-input-form">
              <Text as="h4" mt={0} mb={4} fontWeight="bold">
                MODALITA' DI PAGAMENTO
              </Text>
              <Box>
                <RadioGroup
                  defaultValue="0"
                  onChange={setPaymentMethod}
                  value={paymentMethod}
                >
                  <Stack>
                    {paymentMethods.map((e, idx) => (
                      <Radio
                        size="md"
                        value={idx.toString()}
                        colorScheme="teal"
                        index={idx}
                      >
                        <Box display="inline">
                          {/* <Image
                            src={payinstore}
                            style={{ display: "inline" }}
                            mr={2}
                          /> */}
                          {e}
                        </Box>
                      </Radio>
                    ))}

                    {/* <Radio
                      size="md"
                      value="0"
                      colorScheme="teal"
                      defaultChecked
                    >
                      <Box display="inline">
                        <Image
                          src={payinstore}
                          style={{ display: "inline" }}
                          mr={2}
                        />
                        AL RITIRO IN NEGOZIO
                      </Box>
                    </Radio>
                    <Radio size="md" value="1" colorScheme="teal" isDisabled>
                      <Box display="inline">
                        <Image
                          src={paypal}
                          style={{ display: "inline" }}
                          mr={2}
                        />
                        PAYPAL
                      </Box>
                    </Radio>
                    <Radio size="md" value="2" colorScheme="teal" isDisabled>
                      <Box display="inline">
                        <Image
                          src={creditcard}
                          style={{ display: "inline" }}
                          mr={2}
                        />
                        CREDIT CARD
                      </Box>
                    </Radio> */}
                  </Stack>
                </RadioGroup>
              </Box>
            </Box>
            <MenhirOrderSummary />
          </SimpleGrid>
        )}

        <Center mt={4}>
          <Link to="/checkoutship">
            <Button
              mt={10}
              mb={10}
              backgroundColor="lightgray"
              size="md"
              fontWeight="normal"
              mr={4}
            >
              INDIETRO
            </Button>
          </Link>
          <Link to="/ordercompleted">
            <Button
              onClick={placeOrder}
              mt={10}
              mb={10}
              bg="brand.100"
              color="#ffffff"
              _hover={{ bgColor: "brand.100shade" }}
              size="md"
              fontWeight="normal"
            >
              CONFERMA
            </Button>
          </Link>
        </Center>
      </Box>
    </Flex>
  );
}
