import * as React from "react";
import {
  Box,
  Image,
  Spacer,
  Badge,
  Text,
  Center,
  VStack,
} from "@chakra-ui/react";
import { FaStar, FaHeart, FaCartPlus } from "react-icons/fa";
import "./MiniProduct.css";
import { Link } from "react-router-dom";

const MenhirMiniProduct = (props: any) => {

  const iconPrimaryColor = "teal.500";
  const textGrayColor = "gray.600";
  const iconStar = "yellow.400";
  const iconGrayColor = "gray.400";
  const imgPath = "./demo-images/" + props.imgName.replace("<S>", "small");;
  const minimalView = props.minimalView;
  const price = props.price;
  const discountedPrice =
    props.Discount > 0 ? price - (price * props.discount) / 100 : price;

  let redirectProps = { ...props };
  redirectProps.MinimalView = false;
  const linkTo = {
    pathname: "/product",
    productProps: redirectProps,
  };

  return (
    <Link to={linkTo}>
      <Box
        maxW={{ base: "170px", md: "220px" }}
        minH="250px"
        overflow="hidden"
        bgColor="gray.50"
        className="menhir-mini-product"
        _hover={{ cursor: "pointer" }}
      >
        <Box p="2">
          <VStack align="stretch" justifyContent="space-between">
            <Box d="flex" alignItems="baseline">
              <Box as="h4" isTruncated color="gray.800">
                {props.Title}
              </Box>
              <Spacer />
              {!minimalView && (
                <Box color={iconGrayColor}>
                  <FaHeart />
                </Box>
              )}
            </Box>
            <Box>
              <Center>
                <Image
                  borderRadius="5"
                  align="center"
                  src={require(`${imgPath}`).default}
                />
              </Center>
            </Box>
            {!minimalView && (
              <Box minH={12}>
                <Text
                  as="span"
                  color={textGrayColor}
                  fontSize="xs"
                  textAlign="left"
                >
                  {props.description}
                </Text>
              </Box>
            )}
            <Box d="flex">
              {!minimalView && (
                <Box d="flex" mt="5">
                  {Array(5)
                    .fill("")
                    .map((_, i) => (
                      <Box
                        key={i}
                        color={i < props.rating ? iconStar : iconGrayColor}
                      >
                        <FaStar key={i} />
                      </Box>
                    ))}
                </Box>
              )}
              <Spacer />
              <Box>
                <Text fontSize="md" mt="2" color="gray.800">
                  {props.discount > 0 && (
                    <Badge colorScheme="green" mr="2">
                      -{props.discount}%
                    </Badge>
                  )}
                  <span
                    style={{
                      textDecoration:
                        discountedPrice !== price ? "line-through" : "none",
                      marginRight: "5px",
                      color: props.discount > 0 ? "red" : "gray.800",
                    }}
                  >
                    {new Intl.NumberFormat("it-IT", {
                      style: "currency",
                      currency: "EUR",
                    }).format(props.price)}
                  </span>
                  {discountedPrice !== price &&
                    new Intl.NumberFormat("it-IT", {
                      style: "currency",
                      currency: "EUR",
                    }).format(discountedPrice)}
                </Text>
              </Box>
              {!minimalView && (
                <Box mt="4" ml="2" color={iconPrimaryColor}>
                  <FaCartPlus size={20} />
                </Box>
              )}
            </Box>
          </VStack>
        </Box>
      </Box>
    </Link>
  );
};

export default MenhirMiniProduct;
