import { Box, Center, Spinner, Text, VStack } from "@chakra-ui/react";

export interface BusyProperties {
  Size: "small" | "large";
  Text?: string;
  HideText?: boolean;
}

export default function MenhirPageBusy(busyProp: BusyProperties) {
  const spinnerSize = busyProp.Size === "large" ? "xl" : "sm";
  const busyText = busyProp.Text
    ? busyProp.Text
    : "Solo qualche istante.. stiamo verificando i dati.";
  const hideText = busyProp.HideText === undefined;

  return (
    <Box flex="1" p={0}>
      <Center>
        <VStack>
          <Spinner size={spinnerSize} mt={4} mb={4} />
          {hideText && (
            <Text as="h3" mt={8} mb={4}>
              {busyText}
            </Text>
          )}
        </VStack>
      </Center>
    </Box>
  );
}
