import React, { useState, useEffect } from "react";
import { useLocation, useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import {
  Checkbox,
  Spacer,
  Button,
  Box,
  Flex,
  Text,
  FormControl,
  FormLabel,
  SimpleGrid,
  Table,
  Tr,
  Td,
  Tbody,
  Thead,
  Th
} from "@chakra-ui/react";

import MenhirPageBusy, { BusyProperties } from "./PageBusy";
import axios from "axios";
import { Order, OrderItem } from "./model/TransportOrder";
import { Customer } from "./model/Customer";
import "./OrderDetail.css";
import "./Global.css";

export default function MenhirOrderDetail(props: any) {
  const history = useHistory();
  const location = useLocation();
  const orderId = location.state.orderId;
  const [order, setOrder] = useState<Order>();
  const [busy, setBusy] = useState(false);
  const [customer, setCustomer] = useState<Customer>();
  const [items, setItems] = useState<Array<OrderItem>>([]);
  const [shipped, setShipped] = useState(false);
  const [paid, setPaid] = useState(false);

  const getData = () => {
    setBusy(true);
    axios.defaults.withCredentials = true;
    axios(`${process.env.REACT_APP_BACKEND_URL}/orders?orderId=${orderId}`, {
      method: "get",
    })
      .then((res) => {
        let order: Order = res.data;
        setShipped(order.shipped);
        setPaid(order.paid);
        if (order === null) return;
        setOrder(order);

        // now we read customer data

        axios(
          `${process.env.REACT_APP_BACKEND_URL}/customers?username=${order.email}`,
          {
            method: "get",
          }
        )
          .then((res) => {
            let customer: Customer = res.data;
            if (customer === null) throw new Error("Error");
            setCustomer(customer);
          })
          .catch((r) => {
            alert(`Error: ${r}`);
          })
          .finally(() => setBusy(false));

        // now we read cart

        axios(
          `${process.env.REACT_APP_BACKEND_URL}/orderitems?orderId=${orderId}`,
          {
            method: "get",
          }
        )
          .then((res) => {
            const items: Array<OrderItem> = res.data;
            setItems(items);
          })
          .catch((r) => {
            alert(`Error: ${r}`);
          })
          .finally(() => setBusy(false));

        setBusy(false);
      })
      .catch((r) => {
        alert(`Error: ${r}`);
      })
      .finally(() => setBusy(false));
  };

  const saveOrder = () => {
    let newOrder = { ...order };
    newOrder.shipped = shipped;
    newOrder.paid = paid;

    axios(`${process.env.REACT_APP_BACKEND_URL}/orders`, {
      data: newOrder,
      withCredentials: true,
      method: "post",
    })
      .then((res) => {
        history.push("/admin");
      })
      .catch((err) => {
        alert(`Error updating order ${err}`);
      });    
    
  }

  const formatDate = (dateStr: string) => {
    if (!dateStr) return "";
    const dateObj = new Date(dateStr);
    const options = {
      hour12: false,
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    };
    return dateObj.toLocaleDateString(navigator.language, options);
  };

  const writePaymentMethod = (method: number) => {
    switch (method) {
      case 0:
        return "In negozio";
      case 1:
        return "In contanti";
      case 2:
        return "Carta di credito";
      case 3:
        return "PayPal";
      default:
        return "";
    }
  };

  const writeShippingMethod = (method: number) => {
    switch (method) {
      case 0:
        return "Ririto in negozio";
      case 1:
        return "Consegna a casa";
      default:
        return "";
    }
  };

  useEffect(() => {
    getData();
  }, []);

  const busyProps: BusyProperties = {
    Size: "large",
    Text: "Stiamo caricando i dati..",
  };

  return (
    <Box>
      {busy && <MenhirPageBusy {...busyProps} />}

      <Flex color="white">
        {!busy && (
          <Box flex="1" p={0}>
            <Box>
              <Text as="h3" mt={{ base: 0, md: 5 }}>
                Ordine {order?.orderId}
              </Text>
            </Box>
            <Box mt={5} mb={8}>
              <Box className="order-box">
                <SimpleGrid columns={5} spacing={5} p={2}>
                  <FormControl>
                    <FormLabel>Email</FormLabel>
                    <p>{order?.email}</p>
                  </FormControl>
                  <FormControl>
                    <FormLabel>Data ordine</FormLabel>
                    <p>{formatDate(order?.created)}</p>
                  </FormControl>
                  <FormControl>
                    <FormLabel>Pagamento</FormLabel>
                    <p>
                      {writePaymentMethod(order?.paymentMethod)}
                    </p>
                  </FormControl>
                  <FormControl>
                    <FormLabel>Consegna</FormLabel>
                    <p>
                      {writeShippingMethod(order?.shippingMethod)}
                    </p>
                  </FormControl>
                  <FormControl>
                    <FormLabel>Totale ordine</FormLabel>
                    <p>
                      {new Intl.NumberFormat("it-IT", {
                        style: "currency",
                        currency: "EUR",
                      }).format(order?.total)}
                    </p>
                  </FormControl>
                </SimpleGrid>
              </Box>
              <Box mt={4}>
                <SimpleGrid
                  columns={2}
                  spacing={4}
                  minChildWidth="300px"
                >
                  <Box className="order-box">
                    <SimpleGrid
                      columns={3}
                      spacing={4}
                      p={2}
                      minChildWidth="200px"
                    >
                      <FormControl>
                        <FormLabel>Cliente</FormLabel>
                        <p>
                          <span>{customer?.name}</span>&nbsp;
                          <span>{customer?.lastname}</span>
                        </p>
                      </FormControl>
                      <FormControl>
                        <FormLabel>Telefono</FormLabel>
                        <p>
                          <span>{customer?.phone}</span>&nbsp;
                        </p>
                      </FormControl>
                      <FormControl>
                        <FormLabel>Indirizzo</FormLabel>
                        <p>
                          <span>{customer?.address}</span>&nbsp;<span>{customer?.zipcode}</span>
                        </p>
                      </FormControl>                                
                      <FormControl>
                        <FormLabel>Citt</FormLabel>
                        <p>
                          <span>{customer?.city}</span>
                        </p>
                      </FormControl>                                                      
                    </SimpleGrid>
                  </Box>
                  <Box className="order-box">
                    <Table mt={4} backgroundColor={"white"}>
                        <Thead>
                          <Tr>
                            <Th>Codice prodotto</Th>
                            <Th>Descrizione</Th>
                            <Th>Prezzo</Th>
                            <Th>Quantit</Th>
                          </Tr>
                        </Thead>
                        <Tbody>
                    {
                      items.map((item, index) => (
                        <Tr key={index}>
                        <Td>
                          <Text as="p">{item.code}</Text>
                        </Td>
                        <Td>
                          <Text as="p">{item.title}</Text>
                        </Td>
                        <Td>
                          <Text as="p">{item.price}</Text>
                        </Td>
                        <Td>
                          <Text as="p">{item.quantity}</Text>
                        </Td>                              
                        </Tr>
                      ))
                    }
                    </Tbody>
                    </Table>
                  </Box>
                </SimpleGrid>
              </Box>
              <Box className="menhir-input-form" boxShadow="md" mt={4} display="flex">
                <SimpleGrid columns={5} spacing={5} p={2}>
                  <FormControl>
                    <FormLabel>Pagato</FormLabel>
                    <Checkbox isChecked={paid} onChange={(e) => setPaid(e.target.checked)} />
                  </FormControl>
                  <FormControl>
                    { order?.paymentMethod === 0 && (
                      <FormLabel>Consegnato</FormLabel>
                    )}
                    { order?.paymentMethod === 1 && (
                      <FormLabel>Spedito</FormLabel>
                    )}
                    <Checkbox isChecked={shipped} onChange={(e) => setShipped(e.target.checked)} />
                  </FormControl>
                </SimpleGrid>
                <Spacer />
                <Button bg="brand.100"
                color="#ffffff"
                _hover={{ bgColor: "brand.100shade"}} color="#ffffff" mt={4} onClick={saveOrder}>
                Conferma
              </Button>
              </Box>              
            </Box>
          </Box>
        )}
      </Flex>
    </Box>
  );
}
